"use strict";

import * as React from "react";
import { AppRegistry } from "react-native";

/**
 * rnCommon.js will be the entry point for platform apps
 * Metro bundler will bundle all the dependencies imported in this entry file
 */

const RNCommonApp = () => {
  /**
   * Entry component for the platform app is null
   * A null startup component will prevent metro bundler to add registry component in bundle.
   * Registry component should come from React native app entry file
   */
  return null;
};

/**
 * This is required because else the bundle will throw error
 * "Module AppRegistry is not registered"
 */
AppRegistry.registerComponent("RNCommonApp", () => RNCommonApp);
